import { Controller, TextPoint, EdgePoint, Block } from '@ali/4ever-cangjie';

export default function isAtStartOfBlock(
  anchor: TextPoint | EdgePoint,
  controller: Controller,
) {
  const { document } = controller.value;
  const parent = document.getParent(anchor.key);
  return (
    Block.isBlock(parent) && !parent.data.list && anchor.isAtStartOfNode(parent)
  );
}
